/* FadeInUp */
.os {
  opacity: 0; }
  @media only screen and (max-width: 580px) {
    .os {
      opacity: 1; } }
  .os.animated {
    opacity: 1;
    animation: up 2s cubic-bezier(0.42, 0, 0, 0.995), fade 1s cubic-bezier(0.42, 0, 0, 0.995); }
    @media only screen and (max-width: 580px) {
      .os.animated {
        animation-delay: 0s; } }

@keyframes up {
  0% {
    transform: translateY(100px); }
  100% {
    transform: translateY(0); } }

@keyframes fade {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

/* Reveal */
.reveal.animated {
  position: relative; }
  .reveal.animated::after, .reveal.animated::before {
    content: '';
    height: 100%;
    position: absolute;
    width: 100%; }
  .reveal.animated::before {
    background: #fff;
    -webkit-animation: reveal 1s 50ms cubic-bezier(0.72, 0.16, 0.345, 0.875) both;
    animation: reveal 1s 50ms cubic-bezier(0.72, 0.16, 0.345, 0.875) both;
    z-index: 4; }

@-webkit-keyframes reveal {
  0% {
    transform: translateX(-100%); }
  50% {
    transform: translateX(0); }
  100% {
    transform: translateX(100%); } }

@keyframes reveal {
  0% {
    transform: translateX(-100%); }
  50% {
    transform: translateX(0); }
  100% {
    transform: translateX(100%); } }

@keyframes fade-slider {
  0% {
    opacity: 1;
    transform: translate3d(0%, 0, 0); }
  99% {
    opacity: 1;
    transform: translate3d(0%, 0, 0); }
  100% {
    opacity: 0;
    transform: translate3d(-100%, 0, 0); } }

@-webkit-keyframes reveal-v {
  0% {
    transform: translateY(100%); }
  50% {
    transform: translateY(0); }
  100% {
    transform: translateY(-100%); } }

@keyframes reveal-v {
  0% {
    transform: translateY(100%); }
  50% {
    transform: translateY(0); }
  100% {
    transform: translateY(-100%); } }

